import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { HomePage } from '../home/home';

@Component({
  selector: 'page-slides',
  templateUrl: 'slides.html'
})
export class slides {

  slides = [
    {
      title: "Welcome to Scraps!",
      description: "Have old devices you want to throw away? What about old clothes, toys, books, and other things you might have thrown away? <b>Scraps</b> allows you to donate these things to those in need without having to leave your home!",
      image: "assets/imgs/floppy-disks.jpg",
    },
    {
      title: "Why use Scraps?",
      description: "Using <b>Scraps</b> not only helps you get rid of your old things, it also helps those in need, and the Earth! You can also win <b>competitions</b> and <b>prizes</b> if you recycle enough!",
      image: "assets/imgs/IMG_5570.PNG",
    },
    {
      title: "Did you say Scraps helps the Earth?",
      description: "That's right! By repurposing old technology, clothing, and other products, <b>Scraps</b> reduces the amount of e-waste which winds up in our planet's landfills!",
      image: "assets/imgs/ewasteR.png",
  },
    {
      title: "How do I get started?",
      description: "Scraps is equally as easy to use as it is useful. After creating an account, list your item in the 'Recycle' panel, follow the instructions, and get yourself some points!",
      image: "assets/imgs/IMG_5572.PNG",
    }
  ];

  ///constructor(public navCtrl: NavController) {}

  constructor(public navCtrl:NavController){

  }

  openMain(){
    this.navCtrl.push(HomePage);
  }
}



